import { inject } from 'aurelia-framework';
import { customAttribute } from 'aurelia-templating';
import 'fancybox';

@customAttribute('fancybox')
@inject(Element)
export class AttrFancyboxCustomAttribute {

    constructor(element) {
        this.element = element;
    }

    valueChanged(newValue, oldValue) {

        this.imgHandler = (event) => {
            event.preventDefault();
            let $img = $(event.target);
            var imgs = [];
            var initialIndexOnArray = 0;
            $(this.element).find('img').each(function(index, img) {
                imgs.push({ src: $(img).attr('src'), opts: { caption: $(img).attr('alt') } });
                if (event.target == img) {
                    initialIndexOnArray = index;
                }
            });

            // https://fancyapps.com/fancybox/3/docs/#api
            $.fancybox.open(imgs, {
                i18n: {
                    'zh': {
                        CLOSE: '',
                        NEXT: '',
                        PREV: '',
                        ERROR: '. <br/> .',
                        PLAY_START: '',
                        PLAY_STOP: '',
                        FULL_SCREEN: '',
                        THUMBS: '',
                        DOWNLOAD: '',
                        SHARE: '',
                        ZOOM: ''
                    }
                },
                lang: 'zh',
                loop: true,
                animationEffect: "zoom-in-out", // false zoom fade zoom-in-out
                transitionEffect: "slide", // false fade slide circular tube zoom-in-out rotate
                arrows: true,
                infobar: true,
                buttons: [
                    'slideShow',
                    'fullScreen',
                    'thumbs',
                    // 'share',
                    'download',
                    'zoom',
                    'close'
                ],
            }, initialIndexOnArray);

        };

        $(this.element).on('click', 'img', this.imgHandler);
    }

    bind(bindingContext) {
        this.valueChanged(this.value);
    }

    unbind() {
        window.__debug && console.log('AttrFancyboxCustomAttribute--unbind');
        $(this.element).off('click', 'img', this.imgHandler);
        this.imgHandler = null;
        this.element = null;

        $.fancybox.destroy();
    }
}
