import { bindable, containerless } from 'aurelia-framework';

@containerless
export class EmUserCreate {

    @bindable loginUser;
    @bindable trigger;
    isSuper = nsCtx.isSuper;
    isAdmin = nsCtx.isAdmin;

    triggerChanged(newValue, oldValue) {
        $(this.trigger).click(() => {
            this.emModal.show({
                hideOnApprove: false,
                autoDimmer: true
            });
        });
    }

    showHandler() {
        this._reset();
    }

    _reset() {
        // $(this.frm).form('reset');
        this.username = null;
        this.password = '88888888';
        this.name = null;
        this.mails = null;
    }

    /**
     * DOM
     */
    attached() {
        this._reset();
        $(this.frm)
            .form({
                on: 'blur',
                inline: true,
                fields: {
                    username: {
                        identifier: 'username',
                        rules: [{
                            type: 'empty'
                        }, {
                            type: 'minLength[3]'
                        }, {
                            type: 'regExp',
                            value: /^[a-z]+[a-z0-9\.\-_]*[a-z0-9]+$/,
                            prompt: '.-_,,'
                        }]
                    },
                    password: {
                        identifier: 'password',
                        rules: [{
                            type: 'empty'
                        }, {
                            type: 'minLength[8]'
                        }]
                    },
                    name: {
                        identifier: 'name',
                        rules: [{
                            type: 'empty'
                        }, {
                            type: 'maxLength[20]'
                        }]
                    },
                    mail: {
                        identifier: 'mail',
                        rules: [{
                            type: 'empty'
                        }, {
                            type: 'email'
                        }]
                    }
                }
            });
    }

    approveHandler(modal) {

        if (!this.isAdmin && !this.isSuper) return;

        if ($(this.frm).form('is valid')) {

            let baseURL = utils.getBaseUrl();
            baseURL = _.endsWith(baseURL, '/') ? baseURL : baseURL + '/';
            $.post('/admin/user/save', {
                username: this.username,
                password: this.password,
                name: this.name,
                mail: this.mails,
                enabled: true,
                role: $(this.frm).find('input:radio:checked').val(),
                baseURL: baseURL
            }, (data) => {
                $(this.frm).form('reset');
                modal.hide();
                if (data.success) {
                    toastr.success('!');
                } else {
                    toastr.error(data.data, '!');
                }
            });
        } else {
            modal.hideDimmer();
        }

    }
}
